@use '../../../Styles/variables';


:root{
  #notificationBar {
    z-index: 999999999;
    margin: 10px 2rem;
    text-align: center;
    vertical-align: middle;
    align-content: center;
    border: 0;
    padding: 10px;
    box-shadow: 5px 5px 20px rgba(22, 22, 22, 10%);

    .notificationText {
      font-size: 0.9em;
      letter-spacing: 3px;
      padding-top: 5px;
      padding-bottom: 5px;
      text-transform: uppercase;
    }

    &.success {
      background: linear-gradient(to bottom right, var(--successColorLight) 40%, var(--successColorDark) 100%);
    }

    &.error {
      background: linear-gradient(to bottom left, var(--dangerColorLight) 40%, var(--dangerColorDark) 100%);
    }
  }
}