// default theme
// Get shades using https://mdigi.tools/color-shades/#007b69


:root {
  font-size: 16px;

  --gutter: 12px;
  --gutterLarge: 16px;
  --gutterSmall: 8px;
  --gutterXSmall: 4px;
  
  --sidebarWidth: 260px;
  --navbarHeight: 35px;
  --borderRadius: 12px;
  
  // background
  --bodyBGColor: #abe3fc;;
  --bgColorLight: #fefefe;
  --bgColorDark: #68798e;
  
  --bodyTextColor: #0a261a;
  --textColorLight: #edf6f9;
  --textColorDark: #0c1827;

  --shadowColor: #a6b2c4;
  
  // primary
  --primaryColor: #039393;
  --primaryColorLight: #03939330;
  --primaryColorLighter: #eef6f9;
  --primaryColorDark: #006052;

  // secondary
  --secondaryColor: blue;
  --secondaryColorLight: blue;
  --secondaryColorDark: blue;
  
  // neutral
  --neutralColor: var(--gray-600);
  --neutralColorLight: var(--gray-500);
  --neutralColorDark: var(--gray-700);
  --neutralColorWashed: var(--gray-400);

  // danger
  --dangerColor: #d12f30;
  --dangerColorLight: #db5859;
  --dangerColorDark: #a72526;

  // success
  --successColor: #02875b;
  --successColorLight: #349f7c;
  --successColorDark: #016c49;
  
  // warning
  --warningColor: #f2aa46;
  --warningColorLight: #f5bb6b;
  --warningColorDark: #da993f;

  // disabled
  --disabledColor: var(--gray-500);
  
  // tones of gray
  --white: #fff;
  --gray-100: #fafcfe;
  --gray-200: #f5f8fb;
  --gray-300: #f2f6f9;
  --gray-400: #e5ebef;
  --gray-500: #a6b2c4;
  --gray-600: #68798e;
  --gray-700: #4e5f70;
  --gray-800: #0c1827;
  
  
  // Alerts
  --alert-margin-bot: 1rem;
  

  // scrollbar
  --scrollbarWidth: 0.2rem;
  --scrollbarRadius: 0.25rem;

  @media screen and (max-width: 1280px) {
    font-size: 14px;
  }
}

