@use '../../../Styles/variables';

.table-wrapper{
  overflow-x: auto;
  width: 100%;
  overflow-y: auto;
  .table{
    width: 100%;
    color: var(--bodyTextColor);
    border-collapse: collapse;
  }
  .table-header{
    border-radius: 50px  0 0;
    background-color: var(--primaryColorLight);
    th {
      text-align: left;
      padding: 15px 20px;
    }
  }
  .row{
    box-sizing: border-box;
    border-top: 2px solid var(--primaryColorLighter); 
    td {
      padding: 15px 20px;
    }
  }
  .table-body{
    .row {
      &:hover{
        cursor: pointer;
        background-color: var(--primaryColorLight);
      }
    }
  }
}