@use '../../../Styles/variables';

.overlay-layer {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  -webkit-animation: overlay-fadeIn 0.5s 0.2s forwards;
  -moz-animation: overlay-fadeIn 0.5s 0.2s forwards;
  -o-animation: overlay-fadeIn 0.5s 0.2s forwards;
  animation: overlay-fadeIn 0.5s 0.2s forwards;
  z-index: 1001 !important;
  &.close {
    position: absolute;
    top: 10px;
    right: 0;
  }
  &.full-content {
    .overlay-body {
      margin: 60px auto;
      width: 800px;
      height: 600px;
      padding: 0;
    }
  }

  &.bg-transparent {
    &:before {
      display: none;
    }
  }

  &:before {
    content: ' ';
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    opacity: 0.9;
    background: var(--bodyBGColor)
  }

  &.overlay-body {
    position: absolute;
    left: 0;
    right: 0;
    margin: 60px auto;
    width: 600px;
    background: var(--bodyBGColor);
    border-radius: 10px;
    padding: 50px;
    border: 1px solid var(--bodyBGColor);
    box-shadow: 0 20px 75px rgba(0, 0, 0, 0.13);
    color: var(--primaryTextColor);

    &.small {
      padding: 25px 50px 25px 25px;
      background: var(--bodyBGColor);
      font-size: 16px;
      box-shadow: rgba(0, 0, 0, 0.4) 0 30px 90px;
      .closeButton {
        position: absolute;
        right: 0;
        top: 10px;
        color: var(--primaryTextColor);
        border: var(--bodyBGColor);
      }
    }

    &.error {
      border: var(--dangerColor);
      color: var(--dangerColor);
    }

    &.header {
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 8px;
    }
  }
}


@-webkit-keyframes overlay-fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@-moz-keyframes overlay-fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@-o-keyframes overlay-fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes overlay-fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
