@use '../../../Styles/variables' as *;

header {
  .header-logo{
    padding: 0 var(--gutter);
    width: var(--sidebarWidth);
  }
  .main-header{
    display:flex;
    flex-direction: row;
    flex-grow: 1;
    .header-menu {
      flex-grow: 1;
      display: flex;
      flex-direction: row;
      overflow: hidden;
      white-space: nowrap;
      align-items: baseline;
      justify-content: space-between;
      .menu-item{
        font-weight: bold;
        flex-grow: 1;
        display: block;
        text-align: center;
        padding-left: 1rem;
        padding-right: 1rem;
        white-space: nowrap;
        line-height: var(--navbarHeight);
        border-radius: .25rem;
        margin: var(--gutterSmall) 0;
        position:relative;
        &::after {
          content: '';
          display: block;
          height: 2px;
          width: 0%;
          background-color: #039393;
          transition: width 0.2s ease;
          position: absolute;
          bottom: -5px;
          left: 0;
        }
        &.active{
          color: var(--textColorLight);
          background-color: var(--primaryColor);
          &:hover {
            color: var(--textColor);
            background-color: var(--primaryColorLighter);
          }
        }        
        &:hover{
          background-color: var(--primaryColorLighter);
          &::after {
            width: 100%;
          }


        }
      }
    }
    .header-actions{
      white-space: nowrap;
      align-items: baseline;
      justify-content: space-between;
    }
  }
}