.pageWrapper {
  /* Full height */
  height: 100%;
  /* Center and scale the image nicely */
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  &.v1 {
    background-image: url("../Assets/bg1.png");
  }
  .container {
      position: absolute;
      top: 0;
      bottom: 0;
    right: 0;
      left: 0;
      margin: 60px auto;
      width: 400px;
      .inner {
        background: var(--bodyBGColor);
        min-height: 400px;
        padding: 20px;
      }
  }
}